<template>
  <v-container
    fluid
  >
    <v-row
      v-for="(value, index) in data"
      :key="index"
    >
      <v-col
        cols="12"
        md="10"
      >
        <v-text-field
          v-model="data[index]"
          :label="label"
          required
          @blur="checkRemove(index)"
        />
      </v-col>
      <v-col
        cols="12"
        md="2"
      >
        <v-btn
          right
          color="red"
          @click.prevent="removeRow(index)"
        >
          x
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="10"
      >
        <v-text-field
          v-model="new_row"
          :required="!data"
          :label="label"
          @keydown.enter.prevent="addRow()"
          @paste.prevent="pasteRow"
        />
      </v-col>
      <v-col
        cols="12"
        md="2"
      >
        <v-btn
          right
          color="green"
          @click.prevent="addRow()"
        >
          +
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'ArrayTextFields',
    props: {
      data: {
        type: Array,
        default: function () {
          return []
        },
      },
      label: {
        type: String,
        default: 'Placeholder',
      },
    },
    data () {
      return {
        new_row: '',
        old_data: [],
      }
    },
    watch: {
      data (value) {
        if (this.old_data.length === 0 && value.length > 0) { this.old_data = [].concat(value) } // clone array

        this.checkChanges()
      },
      new_row () {
        this.checkChanges()
        if (this.new_row.length > 0) { this.$store.commit('setChanges', { error: 'new_line', message: 'The new receipt line hasn\'t been added yet.' }) }
      },
    },
    methods: {
      // check if there are still unsaved changes
      checkChanges: function () {
        if (JSON.stringify(this.old_data) !== JSON.stringify(this.data) || this.new_row.length > 0) { this.$store.commit('setChanges', { error: 'changes' }) } else { this.$store.commit('setChanges', { error: false }) }
      },

      // remove with button
      removeRow: function (index) {
        this.data.splice(index, 1)
      },

      // auto remove empty
      checkRemove: function (index) {
        if (this.data[index].trim() === '') {
          this.removeRow(index)
        }
      },

      // add Row by button or ENTER
      addRow: function () {
        if (this.new_row === '') return
        this.data.push(this.new_row)
        this.new_row = ''
      },

      // paste list of Rows separated by newlines
      pasteRow: function (event) {
        let pasted = event.clipboardData.getData('text/plain')
        let rows = pasted.split('\n')

        for (let i = 0; i < rows.length; i++) {
          this.data.push(rows[i])
        }
        this.new_row = ''
      },
    },
  }
</script>

